import React from 'react';
import PropTypes from 'prop-types';
import {Panel, ListItem, Button, Group, Div, Avatar, PanelHeader, View} from '@vkontakte/vkui';
import connect from "@vkontakte/vkui-connect";
import Persik from "./Persik";

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>Example</PanelHeader>
		{fetchedUser &&
		<Group title="User Data Fetched with VK Connect">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
		</Group>}

		<Group title="Navigation Example">
			<Div>
				<Button size="xl" level="2" onClick={go} data-to="persik">
					Show me the Persik, please
				</Button>
			</Div>
		</Group>
	</Panel>
);
class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
		};
	}

	componentDidMount() {
		document.getElementById("test1").innerText=this.get("https://api.finebot.site")
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	get(url){

		var xhr = new XMLHttpRequest();
		xhr.open('GET', url, true);

		xhr.send();
		xhr.onreadystatechange = function() {
			if (this.readyState != 4) return;
			document.getElementById("test1").innerText=xhr.responseText;

			return this.responseText;
			if (this.status != 200) {
				return null;
			}
		}



	}
	render() {
		const { id, go, fetchedUser } =this.props;
		return (
			<Panel id={id}>
				<PanelHeader>Example</PanelHeader>
				{fetchedUser &&
				<Group title="User Data Fetched with VK Connect">
					<ListItem
						before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
						description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
					>
						{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
					</ListItem>
				</Group>}

				<Group title="Navigation Example">
					<Div>
						<Button id="test" size="xl" level="2" onClick={go} data-to="persik">
							Show me the Persik, please
						</Button>
						<Div id="test1">lp[[</Div>
					</Div>
				</Group>
			</Panel>
		);
	}
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default App;
